import {Component, ComponentWillLoad, Element, h, Host, Prop, Watch} from '@stencil/core';
import {HostElement} from '@stencil/core/internal';
import {getAssetsFolder} from '../../services/AssetsFolderHelper';
import {isNotEmpty} from '../../utils/utils';
import {SizeHelper} from '../../services/SizeHelper';

@Component({
  tag: 'soul-icon',
  styleUrl: 'soul-icon.scss',
  shadow: true,
})
export class SoulIcon implements ComponentWillLoad {

  @Prop({reflect: true}) name: string;
  @Prop({reflect: true}) size: '2-xs' | 'xs' | 's' | 'm' | 'l' | 'xl' | '2-xl' | '3-xl' = 's';
  @Prop({reflect: true}) inline: boolean = false;
  @Prop({reflect: true}) color: 'primary' | 'secondary' | 'reduced';
  @Prop({reflect: true}) iconStackUrl: string;
  @Element() hostElement: HostElement;

  private supportedSizes = ['2-xs', 'xs', 's', 'm', 'l', 'xl', '2-xl', '3-xl'];
  private supportedColors = ['primary', 'secondary', 'reduced'];

  private sizeHelper= new SizeHelper();
  private assetsFolder: string;
  private iconUrl: string;

  @Watch('name')
  public nameChange(): void {
    this.setIconUrl();
  }

  @Watch('size')
  public sizeChange(newValue: string): void {
    this.sizeHelper.validateSize(this.supportedSizes, newValue);
  }

  @Watch('color')
  public colorChange(newValue: string): void {
    this.validateColors(newValue);
  }

  render() {
    return (
      <Host>
        <div class='soul-icon__icon' style={{'--_soul-icon-url': this.iconUrl}}></div>
      </Host>
    );
  }

  componentWillLoad(): Promise<void> | void {
    this.assetsFolder = getAssetsFolder(this.hostElement);
    this.setIconUrl();
  }

  private setIconUrl(): void {
    const nameTrimmed = this.name?.trim();
    this.iconUrl = this.iconStackUrl ? `url(${this.iconStackUrl}#${nameTrimmed})` : `url(${this.assetsFolder}/sprite/core-icons.stack.svg#${nameTrimmed})`;
  }

  private validateColors(newValue: string): void {
    if (isNotEmpty(this.color) && !this.supportedColors.includes(newValue)) {
      console.debug(`Color ${newValue} not supported. Supported colors: ${this.supportedColors.join(', ')}. You can use --soul-icon-color to customize icon color.`);
    }
  }

}
